<template>
  <div class="hello container">
    <h1>Scrabble Club Stats</h1>
    <div>
      <label for="customRange2">Minimum winning score: {{mins}}</label>
      <input @change="setMinScore(mins)" v-model="mins" type="range" class="custom-range range-sm" min="0" max="800">
      <label for="customRange2">Maximum losing score: {{maxs}}</label>
      <input @change="setMaxScore(maxs)" v-model="maxs" type="range" class="custom-range range-sm" min="0" max="800">
      <nav aria-label="Page navigation">
          <ul class="pagination">
            <li class="page-item" v-bind:key="page">
              <a @click="getPage(1)" class="page-link" href="#">1</a>
            </li>
            <li v-for="page in [currentPage-1,currentPage,currentPage+1]" class="page-item" v-bind:key="page">
              <a @click="getPage(Math.min(Math.max(page,1),numPages))" class="page-link" href="#">
                {{page==currentPage ? page : page==currentPage-1 ? 'Prev' : 'Next'}}
              </a>
            </li>
            <li class="page-item" v-bind:key="page">
              <a @click="getPage(numPages)" class="page-link" href="#">Last ({{numPages}})</a>
            </li>
          </ul>
      </nav>
      <div class="card-deck" v-for="game in games" v-bind:key="game">
        <div class="gamedata card" >
          <div class="card-body">
            Date: {{game.date.substr(0,10)}}
            <br />Player: {{game.score>game.oppscore ? game.player : game.opp}}
            <br />Score: {{game.score>game.oppscore ? game.score : game.oppscore}}
            <br />Opponent: {{game.score>game.oppscore ? game.opp : game.player}}
            <br />Opp Score: {{game.score>game.oppscore ? game.oppscore : game.score}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import gamedata from '../assets/json/by_club.json';

export default {
  name: 'HelloWorld',
  data() {
    return {
      games: [],
      numResults: 0,
      numPages: 0,
      minScore: -100,
      maxScore: 1000,
      currentPage: 1,
    };
  },
  mounted() {
    this.load();
  },
  methods: {
    load() {
      this.getPage(1);
      this.currentPage = 1;
    },
    getPage(page) {
      const p = page - 1;
      this.currentPage = page;
      this.games = gamedata['calgary scrabble club']
        .filter((game) => (game.score > this.minScore || game.oppscore > this.minScore) &&
          (game.score < this.maxScore || game.oppscore < this.maxScore));
      this.numResults = this.games.length;
      this.numPages = Math.ceil(this.numResults / 5);
      this.games.sort((a, b) => Date.parse(b.date) - Date.parse(a.date));
      this.games = this.games.slice(p * 5, p * 5 + 5);
    },
    setMinScore(score) {
      this.minScore = score;
      this.getPage(1);
    },
    setMaxScore(score) {
      this.maxScore = score;
      this.getPage(1);
    },
  },
};
</script>

